.App {
  /* text-align: center; */
  margin: 6vmin;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.switch-wrapper {
  position: relative;
}

.switch-wrapper > div {
  position: absolute;
}

a {
  text-decoration: none;
  color: #608060;
}

.Back {
  font-size: 10vmin;
  position: fixed;
}

.Til {
  font-size: 5vmin;
}

#Sange ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

#Sange li {
  padding: 1vmin;
  /* width: 80vmin; */
  margin: 0 0 3vmin 0;
	border-bottom: 3pt solid #102010;	
}

#Sange li:last-child {
	border-bottom: 1pt solid #406040;
}

#Sange li:first-child {
	border-top: 1pt solid #406040;
}


body {
  background-color: black;
  color: #608060;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
